.hv3-root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 14px;
  background:
    radial-gradient(circle at 18% 12%, #ffe8bf 0%, #fff3db 36%, transparent 56%),
    radial-gradient(circle at 80% 24%, #d8f2ff 0%, #ecf7ff 36%, transparent 56%),
    #fdf3e3;
}

.hv3-phone {
  position: relative;
  width: 300px;
  height: 620px;
  border-radius: 46px;
  background: #f2f2f3;
  border: 5px solid #cfd0d4;
  overflow: hidden;
  box-shadow: 0 24px 48px rgba(0, 0, 0, 0.18);
}

.hv3-phone::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 26px;
  background: #000;
  border-radius: 999px;
  z-index: 100;
}

.hv3-page {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
}

.hv3-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px 8px;
  font-size: 12px;
  font-weight: 700;
  background: #f2f2f3;
}

.hv3-battery {
  position: relative;
  width: 18px;
  height: 10px;
  border: 1.5px solid #111;
  border-radius: 2px;
  display: inline-flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.hv3-battery__level {
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 1px;
}

.hv3-battery__cap {
  position: absolute;
  right: -3px;
  top: 2px;
  width: 2px;
  height: 4px;
  background: #111;
  border-radius: 1px;
}

.hv3-content {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.hv3-content::-webkit-scrollbar {
  display: none;
}

.hv3-shell {
  border-radius: 30px;
  border: 1px solid #ffd5a5;
  background:
    radial-gradient(circle at 88% 10%, rgba(255, 170, 58, 0.13), transparent 35%),
    radial-gradient(circle at 10% 15%, rgba(80, 180, 255, 0.11), transparent 35%),
    #fff;
  box-shadow: 0 18px 42px rgba(255, 133, 31, 0.12);
  padding: 14px;
}

.hv3-quick-strip {
  display: flex;
  gap: 6px;
  overflow-x: auto;
  padding-bottom: 2px;
  scrollbar-width: none;
}

.hv3-quick-strip::-webkit-scrollbar {
  display: none;
}

.hv3-quick-pill {
  border-radius: 999px;
  border: 1px solid #e4e4e7;
  background: #fafafa;
  padding: 6px 10px;
  font-size: 10px;
  font-weight: 800;
  color: #27272a;
  white-space: nowrap;
}

.hv3-quick-pill--hot {
  background: #fff0db;
  border-color: #ffc98a;
  color: #c2410c;
}

.hv3-banner {
  border-radius: 24px;
  border: 1px solid #ffc184;
  background: linear-gradient(130deg, #fff4e0 0%, #fffcea 100%);
}

.hv3-mission {
  border-radius: 18px;
  border: 1px dashed #f59e0b;
  background: #fffbeb;
}

.hv3-nav {
  flex-shrink: 0;
  margin: 0 8px 8px;
  border: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(12px);
  border-radius: 18px;
  padding: 8px 6px 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
}

.hv3-nav__item {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  color: #3f3f46;
  border-radius: 12px;
  padding: 4px 2px;
}

.hv3-nav__item--active {
  background: #111827;
  color: #fff;
}

.hv3-nav__icon {
  font-size: 14px;
  line-height: 1;
}

.hv3-nav__label {
  font-size: 10px;
  line-height: 1;
  font-weight: 700;
}
